.photo__container{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.arrowDown {
    font-size: 140px;
    color: var(--color-secondary);
}

.wrap {
    position: sticky;
    top: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;
}

.Test{
    width: 100px;
    height: 750px;
    background-color: transparent;
}